import * as React from "react";
import {createStyles, Paper, Typography, withStyles} from "@material-ui/core";
import BaseProps from "../../base/props";

class Game extends React.Component<GameProps, {}> {
    render() {
        const {classes} = this.props;
        return (
            <div>
                <Paper className={classes.root} elevation={1}>
                    <div>
                        <img src="https://media.st.dl.bscstorage.net/steam/apps/779340/header.jpg?t=1540461393" className={classes.band}/>
                        <div>
                            <Typography variant="h3" gutterBottom>
                                GameName
                            </Typography>
                        </div>
                    </div>
                </Paper>
            </div>
        );
    }
}

interface GameProps extends BaseProps {
    classes:{
        root:string,
        band:string
    }
}

const styles = theme => createStyles({
    root: {
        minHeight: 1024,
        marginLeft: 100,
        marginRight: 100,
        marginTop: 100,
        paddingTop:60,
        paddingBottom:60,
        paddingLeft:60,
        paddingRight:60
    },
    band:{
        width:300,
        float:"left"
    }
});
export default withStyles(styles)(Game)
